export default "/* $colors\n ------------------------------------------*/\n.taskboard {\n  overflow-x: auto;\n  overflow-y: hidden;\n  white-space: nowrap;\n  height: calc(100vh - 3.5rem);\n  padding: 1rem;\n}\n.taskboard .taskboard-wrapper {\n  width: 280px;\n  padding-right: 0.5rem;\n  padding-left: 0.5rem;\n  box-sizing: border-box;\n  display: inline-block;\n  vertical-align: top;\n  height: 100%;\n}\n.taskboard .taskboard-wrapper:first-child {\n  padding-left: 0;\n}\n.taskboard .taskboard-wrapper:last-child {\n  padding-right: 0;\n}\n.taskboard .taskboard-list {\n  box-sizing: border-box;\n  display: flex;\n  flex-direction: column;\n  max-height: 100%;\n  white-space: normal;\n  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.07);\n  background-clip: border-box;\n  border-radius: 0.25rem;\n}\n.taskboard .taskboard-header {\n  position: relative;\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  text-transform: uppercase;\n  letter-spacing: 0.02rem;\n  font-size: 13px;\n  padding: 0.5rem 0.5rem 0;\n}\n.taskboard .taskboard-task {\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  background-clip: border-box;\n  border-radius: 0.25rem;\n  padding: 0.5rem;\n  margin-bottom: 0.5rem;\n}\n.taskboard .taskboard-task:last-child {\n  margin-bottom: 0;\n}\n.taskboard .taskboard-task:hover {\n  cursor: -webkit-grab;\n  cursor: grab;\n}\n.taskboard .taskboard-task:after {\n  content: \"\";\n  position: absolute;\n  border: 4px solid transparent;\n  top: 0;\n  border-top-width: 12px;\n  border-bottom-color: transparent;\n  right: 6px;\n}\n.taskboard .taskboard-task.task-status-success:after {\n  border-top-color: #66bb6a;\n  border-right-color: #66bb6a;\n  border-left-color: #66bb6a;\n}\n.taskboard .taskboard-task.task-status-info:after {\n  border-top-color: #29b6f6;\n  border-right-color: #29b6f6;\n  border-left-color: #29b6f6;\n}\n.taskboard .taskboard-task.task-status-warning:after {\n  border-top-color: #ffee58;\n  border-right-color: #ffe